@mixin addAccount {
  .addAccount {
    max-width: 500px;
    width: 90%;
    background-color: $card-color;
    position: fixed;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
    padding: 1rem;
    border-radius: 0.3rem;
    box-shadow: 0 25px 50px rgba(0, 0, 0, 0.1), 0 0 0 1000px $backgroung-color;
    .add-header {
      display: flex;
      justify-content: space-between;
      padding-bottom: 0.5rem;
      margin-bottom: 2rem;
      border-bottom: 1px solid $line_color;
      font-weight: bold;
      font-size: 1.1rem;
      :last-child {
        cursor: pointer;
				font-size: 1.3rem;
      }
    }
    .add-amount {
      display: flex;
      flex-direction: column-reverse;
      justify-content: center;
      align-items: center;
      line-height: 1;
      width: 100%;
      margin-bottom: 1rem;
      label {
        color: $detail-color;
      }
      :last-child {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.3rem;
        line-height: 30px;
      }
      p {
        margin-right: 0.5rem;
      }
    }
    .add-name {
      margin-bottom: 1rem;
      input {
        width: 100%;
      }
    }
    .add-row {
      margin-bottom: 0.5rem;
      label {
        margin-right: 0.5rem;
        color: $detail-color;
      }
    }
    .add-footer {
      margin-top: 1.5rem;
      margin-bottom: 0.5rem;
      display: flex;
      justify-content: center;
      font-size: 2rem;
      cursor: pointer;
    }
  }
}
